/*-- VARIABLES --*/
:root {
  background: rgb(45,184,195);

  --color-bg-backup:rgb(24,78,94);
  /* --color-bg-dark-gradient: radial-gradient(circle, rgba(24,78,94,1) 0%, rgba(8,93,99,1) 97%); */

  --color-bg-dark-gradient: linear-gradient(147deg, rgba(41,149,158,1) 0%, rgba(81,208,151,1) 43%, rgba(2,68,89,1) 100%);
  --color-bg-medium-gradient: linear-gradient(147deg, rgba(41,149,158,1) 0%, rgba(87, 211, 155, 1) 43%, rgba(14, 113, 143,1)100%);
  --color-bg-medium-gradient: rgb(154, 158, 156);

  --color-bg-dark-gradient-transparent: radial-gradient(circle, rgba(24,78,94,.95) 0%, rgba(8,93,99,.95) 97%);
  --color-bg-light-transparent: rgba(255,255,255, 1);

  --color-bg-content-container: linear-gradient( 110.5deg,  rgba(19, 23, 87, 0.7) 50.9%, rgba(15, 13, 104, 0.3) 90% );
  --color-bg-content-container-opaque: linear-gradient( 110.5deg,  rgba(19, 23, 87, 1) 50.9%, rgba(15, 13, 104, 1) 90% );

  --color-accent: rgb(204, 236, 24);
  --color-accent-dark: rgb(63, 110, 94);

  --color-font-dark: rgb(63, 63, 63);

  --font-header: 'Titillium Web', sans-serif;
  --font-title:'Mulish', sans-serif;
  --font-paragraph: 'Poppins', sans-serif;

  /*-- for large containers --*/
  --shadow-primary: 0 9px 40px 0 rgb(0 0 0 / 75%);

  /*-- for large images --*/
  --shadow-image-large: 2px 2px 15px rgb(46, 44, 44);
  /*-- for small images --*/
  --shadow-image-small: 0 9px 14px 0 rgb(0 0 0 / 75%);

  --shadow-button: 8px 8px 8px 0 rgb(0 0 0 / 50%);
  --shadow-button-hover: 4px 4px 4px 0 rgb(0 0 0 / 75%);
}


/*-- GLOBAL STYLES --*/
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 18px;
  scroll-behavior: smooth;
}

@media screen and (min-width: 40em) {
  html {
    font-size: 24px;
    }
}

body {
  background: rgb(24,78,94);
  background: var(--color-bg-backup);
  background: linear-gradient(147deg, rgba(41,149,158,1) 0%, rgba(81,208,151,1) 43%, rgba(2,68,89,1) 100%);
  background: var(--color-bg-dark-gradient);
  font-family: var(--paragraph-font);
  font-size: 1.3125rem;
  line-height: 1.6;
}

.bg-light {
  background-color: rgba(255,255,255, 1);
  background-color: var(--color-bg-light-transparent);
}

.bg-medium {
  background-color: rgb(154, 158, 156);
  background-color: var(--color-bg-medium-gradient);
}

section {
  padding: 2rem 0;
  position: relative;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: min(95%, 90rem);
  color: white;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.bg-dark-card {
  background: rgb(24,78,94);
  background: var(--color-bg-backup);
  background: radial-gradient(circle, rgba(24,78,94,.95) 0%, rgba(8,93,99,.95) 97%);
  background: var(--color-bg-dark-gradient-transparent);
  background: linear-gradient( 110.5deg,  rgba(19, 23, 87, 0.7) 50.9%, rgba(15, 13, 104, 0.3) 90% );
  background: var(--color-bg-content-container);
  box-shadow: 0 9px 40px 0 rgb(0 0 0 / 75%);
  box-shadow: var(--shadow-primary);
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.opaque-card {
  background: rgb(24,78,94);
  background: var(--color-bg-backup);
  background: linear-gradient( 110.5deg,  rgba(19, 23, 87, 1) 50.9%, rgba(15, 13, 104, 1) 90% );
  background: var(--color-bg-content-container-opaque);

  background: rgb(35,143,153);
  background: linear-gradient(315deg, rgba(35,143,153,1) 0%, rgba(55,143,103,1) 43%, rgba(10,48,60,1) 100%);
  background: linear-gradient(150deg, rgba(35,143,153,1) 0%, rgba(55,143,103,1) 43%, rgba(10,48,60,1) 100%);
  box-shadow: 0 9px 40px 0 rgb(0 0 0 / 75%);
  box-shadow: var(--shadow-primary);
}

.btn-grad {background-image: linear-gradient(to right, #5f2c82 0%, #49a09d  51%, #5f2c82  100%)}
.btn-grad {
    margin: 10px 0;
    padding: .3em .3em;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    text-align: center;
    font-weight: bolder;
    font-family: 'Titillium Web', sans-serif;
    font-family: var(--font-header);
    background-size: 200% auto;
    border-radius: 50px;
    box-shadow: 1px 1px 5px black;
    display: block;
    transition: 0.5s;
}

.btn-grad:hover {
  background-position: right center; /* change the direction of the change here */
  text-decoration: none;
}

.btn-small {
  font-size: 1rem;
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  margin: 1rem;
  padding: .2rem 2rem;
  text-decoration: none;
  color: white;
}

.round-borders {
  border-radius: 20px;
}

.shadow-image-large {
  box-shadow: 2px 2px 15px rgb(46, 44, 44);
  box-shadow: var(--shadow-image-large);
}

h1 {
  font-family: 'Titillium Web', sans-serif;
  font-family: var(--font-header);
}

h2,
h3 {
  font-family: 'Mulish', sans-serif;
  font-family: var(--font-title);
}

/*-- HEADER --*/
/* top navigation menu */
.header-section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  width: 100%;
  /* background-image: url('./assets/images/galaxy-bg.jpg'); */
  background-image: url(/portfolio/static/media/galaxy-bg.583dca4c.jpg);
  background-size:cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: white;
}

.header-section::after, .header-details-section::after {
  content: "";
  display: block;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(to top, rgba(6, 27, 34, 0.5) 0%, rgba(4, 48, 51, 0.5) 100%);
}


/*-- HEADER (DETAILS PAGE) --*/
.header-details-section {
  height: 4rem;
  box-shadow: 4px 4px 10px black;
}

.header-details-section::after {
  height: 4rem;
}

.arrow-link {
  z-index: 1;
  padding: .5rem;
  position: absolute;
  bottom: 0;
}

.arrow-link a {
  padding: .6rem;
}

.icon-arrow {
  color: rgba( 255, 255, 255, 0.65 );
  -webkit-filter: drop-shadow(1px 1px 2px rgb(255, 255, 255));
  filter:         drop-shadow(1px 1px 2px rgb(253, 253, 253));
  font-size: 1.4rem;
  transition: all .3s ease-in-out;
}

.arrow-link a:hover .icon-arrow {
    text-decoration: none;
    /* padding: 0px 2rem; */
    font-size: 1.8rem;
    text-shadow: 1px 1px 4px rgba(41, 38, 38, 1);
    color: rgb(204, 236, 24);
    color: var(--color-accent);
}

/*-- ALL CONTENT SECTIONS --*/
.section-title {
  letter-spacing: .1rem;
  font-size: 2rem;
  font-family: 'Mulish', sans-serif;
