:root {
  --font-header: 'Titillium Web', sans-serif;
  --font-title:'Mulish', sans-serif;
  --font-paragraph: 'Poppins', sans-serif;
}

/* CARD STYLES */
.project-card {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    -webkit-flex: 1 1 min(14rem);
            flex: 1 1 min(14rem);
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-content: center;
            align-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 500px;
    position: relative;
    border-radius: 20px;
    box-shadow: var(--shadow-image-small);
    box-shadow: 1px 1px 8px rgb(78, 76, 76);
    overflow:hidden;
    background: rgb(6, 84, 88);
    transition: all 0.5s cubic-bezier(.48, -0.28, 0.41, 1.4);
  }

  .project-card:hover {
    box-shadow: 1px 1px 16px rgb(71, 68, 68);
    overflow:hidden;
  }

  .project-img {
    height: 80%;
  }

  .project-card-body {
    font-size: .8rem;
    padding: .5rem 1.8rem .5rem;
    background-image: linear-gradient(20deg , #2fb92f, #0c7994);
    background: rgb(9,124,133);
    background: linear-gradient(180deg, rgba(9,124,133,1) 0%, rgba(24,78,94,.7), rgba(9, 124,133,1) 80%);
    position: absolute;
    overflow:hidden;
    top: 70%;
    height: 110%;
    box-shadow: 2px -2px 10px rgb(53, 81, 83);
    backdrop-filter: blur( 5.0px );
    -webkit-backdrop-filter: blur(5.0px );
    transition: all 0.6s cubic-bezier(.48, -0.28, 0.41, 1.4);
  }

  .project-card:hover .project-card-body {
    top: 0%;
    overflow:hidden;
  }

  .project-card-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 475px;
    color: #fff;
  }

  .project-card-title {
    letter-spacing: .1rem;
    font-size: 1.7rem;
    font-family: 'Mulish', sans-serif;
    font-family: var(--font-title);
    color: rgb(255, 255, 255);
    padding: .5rem 0 0;
    margin-bottom: .15rem;
    border-bottom: .2rem solid rgba(255, 255, 255, 0.0);
    transition: all .5s cubic-bezier(.48, -0.28, 0.41, 1.4);
  }

  @media screen and (min-width: 40em) {
    .project-card-title {
      font-size: 1.3rem;
    }
  }


  .project-card:hover .project-card-title {
    border-bottom: .2rem solid rgba(255, 255, 255, 1);
  }

  .project-tools {
    font-family: 'Poppins', sans-serif;
    font-family: var(--font-paragraph);
    width: auto;
    display: -webkit-flex;
    display: flex;
    text-align: left;
    opacity: 0;
    transition: all .5s cubic-bezier(.48, -0.28, 0.41, 1.4);
    margin-bottom: 1rem;
  }

  .project-tool-tag {
    font-size: .6rem;
    padding: .1rem .5rem;
    margin: .2rem .4rem 0 0;
    background-color:rgb(204, 236, 24);
    border-radius: 5px;
    color: rgb(63, 62, 62);
    font-weight: 900;
  }

  .project-card:hover .project-tools {
    opacity: 1;
  }

  .project-card-details p {
    font-family: 'Poppins', sans-serif;
    font-family: var(--font-paragraph);
    font-size: 1rem;
    color: white;
    opacity: 0;
    transition: all .5s cubic-bezier(.48, -0.28, 0.41, 1.4);
  }

  .project-card:hover .project-card-body p {
    display:block;
    opacity: 1;
  }

  @media screen and (min-width: 40em) {
    .project-card-details p {
      font-size: .75rem;
    }
  }

  .project-links {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 1rem 0;
  }

  .project-links a {
    -webkit-flex: 1 1 8.5rem;
            flex: 1 1 8.5rem;
    /* min-width: 7rem; */
    text-align: center;
    font-family: 'Titillium Web', sans-serif;
    font-family: var(--font-header);
    font-size: .6rem;
    font-weight:bolder;
    letter-spacing: .06rem;
    border-radius: 30px;
    padding: .3rem;
    margin: .3rem;
    text-decoration: none;
    border: solid .1rem rgba(255, 255, 255, 1);
    color: white;
    background-color: none;
    transition: background-color 350ms ease, color 350ms ease;
  }

  .project-links a:hover {
    background-color: rgb(10, 49, 61);
    border: solid .1rem var(--color-accent);
    color: var(--color-accent);
    text-shadow: var(--shadow-image-small);
  }

  .btn-projects {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }


  body {
    background-color: #eee;
    min-height: 100vh;
  }

