  }

.contact-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 2rem;
    margin: 5rem;
}

.email {
    color: rgb(255, 255, 255);
    font-size: 1.2rem;
}

/*-- SECTION: CONNECT --*/
.contact-links-list {
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
